.blog-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  
    .content-container {
      display: flex;
      flex-direction: column;
      width: 800px;

      a {
        text-decoration: none;
        color: $teal;
        transition: 0.5s ease-in-out;
      
        &:hover {
          color: $dark-teal; 
        }

        h1 {
          font-size: 1.5em;
        }
      }
  
      .featured-image-wrapper {
        img {
          margin: 15px 0px;
          width: 100%;
          height: 350px;
          border-radius: 5px;
          object-fit: cover;
        }
      }
    }
  }